
//vuex状态管理
import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import action from './action.js';
//全局注册
Vue.use(Vuex);

//创建store对象
const store = new Vuex.Store({
  state: {
    topics: [
      { name: 'all', desc: '全部' },
      { name: 'good', desc: '精华' },
      { name: 'share', desc: '分享' },
      { name: 'ask', desc: '问答' },
      { name: 'job', desc: '招聘' },
      { name: 'dev', desc: '测试' },
    ],
    //未登录时更多菜单
    unLoginedMoreMenus: {
      login: '登录'
    },
 
    //登录时的更多菜单
    loginedMoreMenus: {
      create:'发表新话题',
      center: '个人中心',
      msg: '消息通知',
      logout: '退出'
    },
    //是否登录
    isLogined: false,
    //登陆的用户名
    loginname:'',
    //accesstoken码
    accesstoken:'',
    //登陆用户id
    author_id:'',
    //是否显示
    isShowMoreMenus: false
  },
  //状态数据计算属性
  getters: {
    moreMenus: function (state) {
      if (state.isLogined) {
        return state.loginedMoreMenus;
      }
      return state.unLoginedMoreMenus;
    },
    getTopicDescByTab:function(state){
      //需要返回一个函数结构
      return function(tab){
        for(var index=0;index<state.topics.length;index++){
          if(state.topics[index].name==tab){
            return state.topics[index].desc;
          }
        }
      }
    }
  },
  mutations,
  action

});

//对外输出模块
export default store;


