import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '../components/Home.vue'
import Topiclist from '../components/Topiclist.vue'
import Topic from '../components/Topic.vue'
import Login from '../components/Login.vue'
import Logout from '../components/Logout.vue'
import Center from '../components/Center.vue'
import Msg from '../components/Msg.vue'
import User from '../components/User.vue'
import Create from '../components/Create.vue'
import Edit from '../components/Edit.vue'

Vue.use(VueRouter);

const routes=[
 {  path:'/',redirect:'/topiclist/all'},
  // { path:'/',name:'home', component:Home},
  {path:'/topiclist/:name',name:'topiclist',component:Topiclist,props:true},
  {path:'/topic/:id',name:'Topic',component:Topic,props:true},
  {path:'/login',name:'login',component:Login,props:true},
  {path:'/center',name:'center',component:Center,props:true},
  {path:'/user/:loginname',name:'user',component:User,props:true},
  {path:'/msg',name:'msg',component:Msg,props:true},//消息通知
  {path:'/logout',name:'logout',component:Logout,props:true},//登出操作
  {path:'/create',name:'create',component:Create} ,//发表新话题
  {path:'/topic/:id/edit',name:'edit',component:Edit,props:true} //编辑主题

];
//创建路由管理器
const router = new VueRouter({
  routes
});

export default router;



