
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mavonEditor} from 'mavon-editor';
import 'mavon-editor/dist/css/index.css';
export default {
name: "edit",
props: ["id"],
data: function() {
return {
topic: {
title: "",
content: ""
}
};
},

methods: {

edittopic: function() {
var app = this;
console.log("topic_id=" + this.topic.id);
console.log("title=" + this.topic.title);
console.log("content=" + this.topic.content);
console.log("accesstoken=" + this.$store.state.accesstoken);
this.$http
.post("/topics/update", {
topic_id: this.topic.id,
title: this.topic.title,
content: this.topic.content,
accesstoken: this.$store.state.accesstoken,
tab: "dev"
}).then(function(response) {


app.$router.back();
});
}
},
components: {
XTextarea,
Group,
XButton,
mavonEditor
},
beforeRouteEnter: function(to, from, next) {
next(function(vm) {
vm.$http.get("/topic/" + to.params.id).then(function(response) {
vm.topic = response.data.data;
});
});
},
beforeRouteUpdate: function(to, from, next) {
next(function(vm) {
app.$http.get("/topic/" + to.params.id).then(function(response) {
app.topic = response.data.data;
});
});
}
};



